import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { BtnSave } from 'components/common/button';
import FormInfoCommon from './FormInfoCommon';
import TableClassList from './TableClassList';
import './style.scss';

class ClassListPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Danh sách lớp học">
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSave />
              <BtnSave title="Phân phòng tự động" />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableClassList />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassListPage />
  </Page>
);



