import React, { Component } from 'react';
import { Table, Select, Input } from 'antd';
import { setTableClassList, setShiftSchool, setClassRoom } from 'dump/data';

class TableClassList extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    const { Option } = Select;

    this.columns = [
      {
        title: 'Lớp',
        dataIndex: 'lop',
        key: 'lop',
      },
      {
        title: 'Số SV',
        dataIndex: 'soSV',
        render: () => <Input style={{ width: '100%' }} />,
      },
      {
        title: 'Ca học dự kiến',
        dataIndex: 'ca_hoc_du_kien',
        render: () =>
          setShiftSchool.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setShiftSchool.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Phòng học',
        dataIndex: 'phong_hoc',
        render: () =>
          setClassRoom.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setClassRoom.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Hệ đào tạo',
        dataIndex: 'heDaoTao',
        key: 'heDaoTao',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'tenKhoa',
        key: 'tenKhoa',
      },
      {
        title: 'Khóa học',
        dataIndex: 'khoaHoc',
        key: 'khoaHoc',
      },
      {
        title: 'Tên ngành',
        dataIndex: 'tenNganh',
        key: 'tenNganh',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={setTableClassList}
        columns={this.columns}
      />
    );
  }
}

export default TableClassList;



