import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { setShiftSchool } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Ca học:">
              {getFieldDecorator('shiftSchool', {})(
                <Select>
                  {setShiftSchool.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ 'margin-top': '19px' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <br />
        <Row gutter={24}>
          <Col span={6}>
            Số lớp ca sáng: <b>3</b>
          </Col>
          <Col span={6}>
            Số lớp ca chiều: <b>2</b>
          </Col>
          <Col span={6}>
            Số lớp ca tối: <b>1</b>
          </Col>
          <Col span={6}>
            Tổng số sinh viên: <b>426</b>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommon;



